import { useEffect, useState } from "react";
import { Alert, Badge, Button, Card, Modal } from "react-bootstrap";


export default function({onDelete, onCancel, remain, show, ...props}) {
    
    const [counter,setCounter] = useState(4);

    useEffect(() => {
        console.log(remain);
    },[remain])

    useEffect(() => {
        if(counter > 0) {
            setTimeout(() => {
                setCounter(counter-1);
            },1000);
        }
    },[counter])

    return (
        <Modal show={show} centered>
            <Modal.Header style={{background: '#FFA8A8'}}>
                <Modal.Title> </Modal.Title>
            </Modal.Header>
            <Modal.Body >
                <Alert variant="danger">    !</Alert>
                <p>     .      </p>
                <p>    -   ""</p>
            </Modal.Body>
            <Modal.Footer >
                <Button variant="secondary" onClick={onCancel}></Button>
                <Button variant="danger" onClick={onDelete} disabled={counter > 0}>
                    <span>{counter > 0 && <Badge bg="secondary">{counter}</Badge>} </span></Button>
            </Modal.Footer>
        </Modal>
    )

}